<template>
	<drawer-bottom
		ref='drawerRef'
		title="结算方式"
	>
		<view class="settlement-ways">
			<view v-for="(item, index) in settlementWayList" :key="index" class="settlement-way-item" @click="toggleSettlementWay(index)">
				<radio class="settlement-radio" :checked="checkValue === item.value" :color="themeColor"></radio>
				<view class="settlement-text">{{ item.label }}</view>
			</view>
		</view>
	</drawer-bottom>
</template>

<script>
	import DrawerBottom from '@/components/drawer-bottom/index.vue'
	import { dateFormat } from '@/frame/utils/date-format'
	import { SettlementWayEnum } from '@/utils/enum.js'
	import { convertEnumToList } from '@/frame/utils/enum'
	import { themeColor } from 'uni.scss'
	
	export default {
		components: {
			DrawerBottom
		},
		data() {
			return {
				settlementWayList: convertEnumToList(SettlementWayEnum, 'select-list'),
				checkValue: null,
				themeColor
			}
		},
		methods: {
			show(checkValue) {
				this.$refs.drawerRef.show()
				this.checkValue = checkValue
			},
			hide() {
				this.$refs.drawerRef.hide()
			},
			toggleSettlementWay(index) {
				const item = this.settlementWayList[index]
				this.checkValue = item.value
				this.$emit('confirm', item)
				this.hide()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.settlement-ways {
		padding: 30upx;
	}
	
	.settlement-way-item {
		border: 2upx solid #A3A7B3;
		border-radius: 16upx;
		padding: 30upx;
		@include flex-c-center;
		font-size: 28upx;
		
		.settlement-text {
			margin-left: 20upx;
		}
		
		.settlement-radio {
			transform: scale(.8);
		}
		
		&.active {
			background-color: $theme-color-bg;
			border-color: #theme-color;
			
			.settlement-text {
				color: #333333;
			}
		}
		
		& + .settlement-way-item {
			margin-top: 30upx;
		}
	}

</style>
